// Inline Text Style
.bBlocksTextHighlight {
	color: #333;
	background-color: #beff14;
	padding: .2em;
	border-radius: 3px;
}

.bBlocksTextUppercase {
	text-transform: uppercase;
}

.bBlocksTextUnderline {
	text-decoration: underline;
}

.bBlocksTextOverline {
	text-decoration: overline;
}